import React, { useState } from 'react';
import { useTonConnectUI } from '@tonconnect/ui-react';

function App() {
    const [walletAddress, setWalletAddress] = useState(null);
    const [userBalance, setUserBalance] = useState(0);

    //  
    const gifts = [
        { name: " 1", price: 10, image: "https://via.placeholder.com/150" },
        { name: " 2", price: 20, image: "https://via.placeholder.com/150" },
        { name: " 3", price: 30, image: "https://via.placeholder.com/150" }
    ];

    //    ( )
    const mainWalletAddress = 'UQAGitCFQNH1JV2s7p6xomRZepCXk93fo11f3SaHH1P6l_-z';

    //  Hook  @tonconnect/ui-react
    const [tonConnectUI] = useTonConnectUI();

    //   
    const connectWallet = async () => {
        try {
            const wallets = await tonConnectUI.getWallets();
            if (!wallets || wallets.length === 0) {
                alert("     .");
                return;
            }

            //    ()
            const wallet = await tonConnectUI.connect({ bridgeUrl: wallets[0].bridgeUrl });
            if (wallet && wallet.account) {
                setWalletAddress(wallet.account.address);
                alert(`   : ${wallet.account.address}`);
            }
        } catch (error) {
            console.error("    :", error);
            alert(`    : ${error.message}`);
        }
    };

    //   
    const depositFunds = async () => {
        const amount = parseFloat(prompt("    :"));
        if (!amount || amount <= 0) {
            alert("   .");
            return;
        }

        const transaction = {
            validUntil: Math.floor(Date.now() / 1000) + 360,
            messages: [
                {
                    address: mainWalletAddress,
                    amount: (amount * 1_000_000_000).toString() //    nanoTON
                }
            ]
        };

        try {
            await tonConnectUI.sendTransaction(transaction);
            setUserBalance(userBalance + amount);
            alert(`    ${amount} TON.`);
        } catch (error) {
            alert(`    : ${error.message}`);
        }
    };

    //   
    const buyGift = (price) => {
        const fee = price * 0.01; // 1% 
        const totalCost = price + fee;

        if (userBalance < totalCost) {
            alert("     .");
            return;
        }

        setUserBalance(userBalance - totalCost);
        alert(`    ${price} TON +  ${fee.toFixed(2)} TON.`);
    };

    //   
    const sellGift = () => {
        const sellPrice = 10; //   
        const fee = sellPrice * 0.01; // 1% 
        const netAmount = sellPrice - fee;

        setUserBalance(userBalance + netAmount);
        alert(`    ${netAmount.toFixed(2)} TON   .`);
    };

    return (
        <div className="App container text-center mt-5">
            <h1 className="text-primary">    !</h1>

            {/*     */}
            {!walletAddress && (
                <div>
                    <p className="text-muted">    .</p>
                    <button className="btn btn-success btn-lg mb-3" onClick={connectWallet}>
                         
                    </button>
                </div>
            )}

            {/*     */}
            {walletAddress && (
                <div>
                    <p className="text-success"> : {walletAddress}</p>
                    <p className="text-success"> : {userBalance.toFixed(2)} TON</p>

                    {/*    */}
                    <div className="mb-4">
                        <h3 className="text-info"> </h3>
                        <button className="btn btn-warning btn-block" onClick={depositFunds}>
                             
                        </button>
                    </div>

                    {/*   */}
                    <div>
                        <h3> :</h3>
                        <div className="d-flex justify-content-center">
                            {gifts.map((gift, index) => (
                                <div key={index} className="gift m-2 p-3 border rounded">
                                    <img src={gift.image} alt={gift.name} width="100" />
                                    <h5>{gift.name} - {gift.price} TON</h5>
                                    <button className="btn btn-primary" onClick={() => buyGift(gift.price)}>
                                         
                                    </button>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/*    */}
                    <div className="mt-4">
                        <h3 className="text-danger"> </h3>
                        <p> : 10 TON</p>
                        <button className="btn btn-danger btn-block" onClick={sellGift}>
                             
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default App;
